import React, { useState, useEffect } from "react";

// API
import apis from "../../../apis";

/**
 * 
 * @param postType  ()
 * @param setPostTopic  PO 
 * @param setShowChoosePop -() 
 * @param setShowMainPop -- 
 */
export default function ChooseTopic({
  postType,
  setPostTopic,
  setShowChoosePop,
  setShowMainPop,
}) {
  const [topicList, setTopicList] = useState([]) // ()

  // choose pop  main pop
  const afterChoose = (topic) => {
    setPostTopic(topic); //  PO 
    setShowChoosePop(0);
    setShowMainPop(1);
  };

  // 
  useEffect(() => {
    const getTopicsWithType = async () => {
      const res = await apis.TopicApi.getTopicsWithType(postType.id);
      setTopicList(res.data);
    };
    getTopicsWithType();
    return () => {};
  }, [postType]);

  return (
    <div className="pop-choose-child-topic d-flex my-3">
      {topicList.map((topic, index) => (
        <div
          key={index}
          className="choose-child-container py-2 px-3 m-1 fs-lgs bg-gray-5 text-dark-green"
          onClick={() => afterChoose(topic)}
        >
          {topic.topicName}
        </div>
      ))}
    </div>
  );
}
