import React, { useState } from "react";

// react Calendar 
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

/**
 * 
 * @param setEnd  PO 
 * @param setShowChoosePop -() 
 * @param setShowMainPop -- 
 */
export default function ChooseEnd({
  setEnd,
  setShowChoosePop,
  setShowMainPop,
}) {
  const [date, setDate] = useState(new Date()); // State

  //  
  const clickCalendat = (date) => {
    let year = date.getFullYear();
    let month = date.getMonth();
    let day = date.getDate();
    setDate(date);
    setEnd(`${year}-${month}-${day}`);
    setShowChoosePop(0);
    setShowMainPop(1);
  };
  return (
    <div className="pop-choose-end d-flex jc-center my-3">
      <Calendar onChange={clickCalendat} value={date} />
    </div>
  );
}
