import React from "react";
import Config from "../../Config";
const { BASEURL } = Config;

/**
 *
 * @param myInfo user info
 * @param postTitle PO 
 * @param setPostTitle PO 
 * @param setShowChoosePop -() 
 * @param setShowMainPop -- 
 * @param setWhichPopChoose choose pop(...) 
 * @param setChooseTitle choose pop () 
 * @param postTopic PO 
 * @param begin PO 
 * @param end PO 
 */
export default function PopPostTop({
  userInfo,
  postTitle,
  setPostTitle,
  setShowChoosePop,
  setShowMainPop,
  setWhichPopChoose,
  setChooseTitle,
  postTopic,
  begin,
  end,
}) {
  const setChildPopShow = () => {
    setWhichPopChoose(0); //  " pop" 
    setShowChoosePop(1); // choose pop
    setShowMainPop(0); // Main pop 
    setChooseTitle(""); // choose pop 
  };

  return (
    <div className="pop-post-top-content d-flex flex-col">
      <div className="pop-top-self d-flex ai-center mb-3">
        {/* user  */}
        <img
          className="pop-top-self-img mr-2"
          src={userInfo.pictureUrl
            ? `${BASEURL}${userInfo.pictureUrl}`
            : require("../../assets/image/profile.png")}
          alt=""
        />

        {/* --(user) */}
        <div className="pop-top-self-info flex-1">
          <p className="pop-top-self-name m-0 pb-1">{userInfo.username}</p>
          <p className="pop-top-self-time m-0">
            {begin}~{end}
          </p>
        </div>

        {/* PO  */}
        <div
          className="pop-top-choose p-2 fs-lgs bg-gray-5 text-dark-green"
          onClick={setChildPopShow}
        >
          {postTopic.topicName}
        </div>
      </div>

      {/* PO  */}
      <input
        type="text"
        className="pop-top-title fs-lgs pb-2"
        placeholder=""
        value={postTitle}
        onChange={(event) => setPostTitle(event.target.value)}
      ></input>
    </div>
  );
}
