import React, { useState, useEffect } from "react";
import NavMenuChild from "./navMenuChild";

//  
const navLinks = [
  {
    title: "",
    img: "info.png",
    lineColor: "#26B6C3",
    path: "/home/info?topic=food",
  },
  {
    title: "",
    img: "need.png",
    lineColor: "#EF7577",
    path: "/home/need?topic=food",
  },
  {
    title: "",
    img: "location.png",
    lineColor: "#EF7577",
    path: "/map",
  },
  {
    title: "",
    img: "profile.png",
    lineColor: "#7383BF",
    path: "/home/profile",
  },
  {
    title: "",
    img: "more.png",
    lineColor: "#3399FF",
    path: "",
  },
];

/**
 * bar 
 * @param location URL 
 * @param leftMenuActive 
 * @param setLeftMenuActive 
 */
export default function NavMenu({
  location,
  leftMenuActive,
  setLeftMenuActive,
}) {
  const [menuActive, setMenuActive] = useState(0); // menu child()

  // location   menu child 
  useEffect(() => {
    const pathArr = location.pathname.split("/");
    const lastPathName = pathArr.pop();
    //  child 
    navLinks.every((link, index) => {
      if (lastPathName === link.path.split("/").pop()) {
        setMenuActive(index);
        return false
      }

      //  URL 
      if (index === 0 || index === 1) {
        const splitLinkPathName = link.path.split("/").pop().split("?")[0];
        if (lastPathName === splitLinkPathName) {
          setMenuActive(index);
          return false
        }
      }

      //  -1
      setMenuActive(-1)
      return true
    });
  }, [location]);

  return (
    <div className="nav-menu-container d-flex jc-center">
      <ul className="menu d-flex jc-around p-0 m-0">
        {navLinks.map((link, index) => (
          //   child
          <NavMenuChild
            key={index}
            link={link}
            index={index}
            menuActive={menuActive}
            leftMenuActive={leftMenuActive}
            setLeftMenuActive={setLeftMenuActive}
            location={location}
          ></NavMenuChild>
        ))}
      </ul>
    </div>
  );
}
