import React from "react";

// components
import LeftNavChild from "./leftNavChild";

// path infoneedprofile 
// 
const navMenu = [
  // {
  //   title: "Name",
  //   img: "logo_pure.png",
  //   path: "/home/profile",
  // },
  {
    title: "",
    img: "first_menu_food.png",
    path: "?topic=food",
    bgColor: "bg-pink-1",
  },
  {
    title: "",
    img: "first_menu_cloth.png",
    path: "?topic=cloth",
    bgColor: "bg-green",
  },
  {
    title: "",
    img: "first_menu_home.png",
    path: "?topic=home",
    bgColor: "bg-brown",
  },
  {
    title: "",
    img: "first_menu_transportation.png",
    path: "?topic=transportation",
    bgColor: "bg-blue-3",
  },
  {
    title: "",
    img: "first_menu_education.png",
    path: "?topic=education",
    bgColor: "bg-yellow-1",
  },
  {
    title: "",
    img: "first_menu_entertainment.png",
    path: "?topic=entertainment",
    bgColor: "bg-purple",
  },
  {
    title: "",
    img: "first_menu_health.png",
    path: "?topic=health",
    bgColor: "bg-orange",
  },
];
/**
 * 
 * @param postType  (...)
 * @param setpostType 
 * @param leftMenuActive 
 * @param location URL 
 *   
 */
export default function LeftNavigation({
  postType,
  setpostType,
  leftMenuActive,
  location,
}) {
  return (
    <div
      className={`left-navigation ${leftMenuActive === 0 ? "" : "left-open"}`}
    >
      <ul className="side-menu-container p-0 pl-1 pt-2 m-0">
        {navMenu.map((choice, index) => (
          //  menu child
          <LeftNavChild
            key={index}
            postType={postType}
            setpostType={setpostType}
            index={index}
            choice={choice}
            location={location}
          ></LeftNavChild>
        ))}
      </ul>
    </div>
  );
}
