import React, { useState } from "react";

// react Calendar 
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

/**
 *
 * @param setBegin  PO 
 * @param setShowChoosePop -() 
 * @param setShowMainPop -- 
 */
export default function ChooseBegin({
  setShowChoosePop,
  setShowMainPop,
  setBegin,
}) {
  const [date, setDate] = useState(new Date()); // State

  //  
  const clickCalendat = (date) => {
    let year = date.getFullYear();
    let month = date.getMonth();
    let day = date.getDate();
    setDate(date);
    setBegin(`${year}-${month}-${day}`);
    setShowChoosePop(0);
    setShowMainPop(1);
  };

  return (
    <div className="pop-choose-begin d-flex jc-center my-3">
      <Calendar onChange={clickCalendat} value={date} />
    </div>
  );
}
